<template>
    <div class="decrypt-container">
        <div class="header">
            <h1>{{ msg }}</h1>
        </div>
        
        <div class="content">
            <div class="input-section">
                <label for="ciphertext">密文：</label>
                <textarea
                    id="ciphertext"
                    v-model="ciphertext"
                    placeholder="请输入需要解密的参数的密文..."
                    class="textarea-input"
                    rows="14"
                ></textarea>
            </div>
            
            <div class="button-section">
                <button 
                    @click="handleDecrypt" 
                    :disabled="!ciphertext.trim() || isDecrypting"
                    class="decrypt-btn"
                >
                    {{ isDecrypting ? '解密中...' : '解密' }}
                </button>
                <button 
                    @click="handleClear" 
                    class="clear-btn"
                >
                    清空
                </button>
            </div>
            
            <div class="output-section">
                <label for="plaintext">明文：</label>
                <textarea
                    id="plaintext"
                    v-model="plaintext"
                    placeholder="解密后的明文将显示在这里..."
                    class="textarea-output"
                    rows="14"
                    readonly
                ></textarea>
            </div>
            
            <div v-if="errorMessage" class="error-message">
                {{ errorMessage }}
            </div>
        </div>
    </div>
</template>

<script>
import { AppRequestDecryptor } from '../utils/AppRequestDecryptor.js'

export default {
    name: 'DecryptTool',
    props: {
        msg: String
    },
    data() {
        return {
            ciphertext: '',
            plaintext: '',
            isDecrypting: false,
            errorMessage: ''
        }
    },
    methods: {
        handleDecrypt() {
            if (!this.ciphertext.trim()) {
                this.errorMessage = '请输入密文'
                return
            }
            
            this.isDecrypting = true
            this.errorMessage = ''
            this.plaintext = ''
            
            // 使用 setTimeout 确保 UI 更新，让加载状态能够显示
            setTimeout(() => {
                try {
                    const result = AppRequestDecryptor.decrypt(this.ciphertext.trim())
                    
                    // 将解密结果格式化为易读的 JSON 字符串
                    const formattedResult = JSON.stringify(result, null, 2)
                    this.plaintext = formattedResult
                } catch (error) {
                    this.errorMessage = error.message || '解密失败，请检查密文格式是否正确'
                    this.plaintext = ''
                    console.error('解密错误:', error)
                } finally {
                    this.isDecrypting = false
                }
            }, 0)
        },
        handleClear() {
            this.ciphertext = ''
            this.plaintext = ''
            this.errorMessage = ''
        }
    }
}
</script>

<style scoped>
.decrypt-container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
}

.header {
    text-align: center;
    margin-bottom: 30px;
}

.header h1 {
    color: #2c3e50;
    font-size: 28px;
    font-weight: 600;
    margin: 0;
}

.content {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.input-section,
.output-section {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

label {
    font-weight: 500;
    color: #333;
    font-size: 14px;
}

.textarea-input,
.textarea-output {
    width: 100%;
    padding: 12px;
    border: 1px solid #ddd;
    border-radius: 6px;
    font-size: 14px;
    font-family: 'Courier New', monospace;
    resize: vertical;
    transition: border-color 0.3s, box-shadow 0.3s;
    box-sizing: border-box;
}

.textarea-input:focus {
    outline: none;
    border-color: #409eff;
    box-shadow: 0 0 0 2px rgba(64, 158, 255, 0.1);
}

.textarea-output {
    background-color: #f5f7fa;
    color: #606266;
}

.textarea-input::placeholder,
.textarea-output::placeholder {
    color: #c0c4cc;
}

.button-section {
    display: flex;
    gap: 12px;
    justify-content: center;
}

.decrypt-btn,
.clear-btn {
    padding: 10px 24px;
    font-size: 14px;
    font-weight: 500;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s;
}

.decrypt-btn {
    background-color: #409eff;
    color: white;
    min-width: 100px;
}

.decrypt-btn:hover:not(:disabled) {
    background-color: #66b1ff;
}

.decrypt-btn:active:not(:disabled) {
    background-color: #3a8ee6;
}

.decrypt-btn:disabled {
    background-color: #c0c4cc;
    cursor: not-allowed;
    opacity: 0.6;
}

.clear-btn {
    background-color: #909399;
    color: white;
    min-width: 100px;
}

.clear-btn:hover {
    background-color: #a6a9ad;
}

.clear-btn:active {
    background-color: #82848a;
}

.error-message {
    padding: 12px;
    background-color: #fef0f0;
    border: 1px solid #fde2e2;
    border-radius: 6px;
    color: #f56c6c;
    font-size: 14px;
    text-align: center;
}

@media (max-width: 768px) {
    .decrypt-container {
        padding: 15px;
    }
    
    .header h1 {
        font-size: 24px;
    }
    
    .button-section {
        flex-direction: column;
    }
    
    .decrypt-btn,
    .clear-btn {
        width: 100%;
    }
}
</style>