/**
 * Base64 工具类
 */
class Base64 {
    /**
     * Base64 解码
     * @param {string} s - Base64 编码的字符串
     * @returns {Uint8Array} 解码后的字节数组
     */
    static decode(s) {
        // 使用 node-forge 的 Base64 解码
        const decoded = forge.util.decode64(s);
        // forge.util.decode64 返回二进制字符串，需要转换为 Uint8Array
        return new Uint8Array(
            decoded.split('').map(c => c.charCodeAt(0))
        );
    }

    /**
     * Base64 编码
     * @param {Uint8Array|ArrayBuffer|string} data - 要编码的数据
     * @returns {string} Base64 编码的字符串
     */
    static encode(data) {
        if (typeof data === 'string') {
            return forge.util.encode64(data);
        }
        let uint8Array;
        if (data instanceof ArrayBuffer) {
            uint8Array = new Uint8Array(data);
        } else if (data instanceof Uint8Array) {
            uint8Array = data;
        } else {
            uint8Array = new Uint8Array(data);
        }
        const binary = Array.from(uint8Array)
            .map(byte => String.fromCharCode(byte))
            .join('');
        return forge.util.encode64(binary);
    }
}

/**
 * RSA 工具类
 */
class RSAUtils {
    /**
     * RSA 最大解密密文大小（128字节）
     */
    static MAX_DECRYPT_BLOCK = 128;

    /**
     * 使用私钥解密数据
     * @param {Uint8Array|ArrayBuffer} encryptedData - 已加密的数据
     * @param {string} privateKey - 私钥（Base64编码的 PKCS8 格式）
     * @returns {Uint8Array} 解密后的数据
     */
    static decryptByPrivateKey(encryptedData, privateKey) {
        try {
            // 确保 encryptedData 是 Uint8Array
            let dataArray;
            if (encryptedData instanceof ArrayBuffer) {
                dataArray = new Uint8Array(encryptedData);
            } else if (encryptedData instanceof Uint8Array) {
                dataArray = encryptedData;
            } else if (typeof encryptedData === 'string') {
                // 如果是字符串，转换为 Uint8Array
                dataArray = new Uint8Array(encryptedData.split('').map(c => c.charCodeAt(0)));
            } else {
                // 尝试转换为 Uint8Array
                dataArray = new Uint8Array(encryptedData);
            }

            // 解码私钥（Base64 -> DER 格式）
            const keyBuffer = Base64.decode(privateKey);
            
            // 将 Uint8Array 转换为 forge 需要的二进制字符串
            const keyBinary = Array.from(keyBuffer)
                .map(byte => String.fromCharCode(byte))
                .join('');
            
            // 使用 node-forge 解析 DER 格式的 PKCS8 私钥
            let privateKeyForge;
            try {
                // 尝试从 DER 格式解析 PKCS8 私钥
                const asn1 = forge.asn1.fromDer(keyBinary);
                // PKCS8 格式的私钥需要先解包
                privateKeyForge = forge.pki.privateKeyFromAsn1(asn1);
            } catch (error1) {
                try {
                    // 如果直接解析失败，尝试从 PEM 格式解析
                    const pemKey = this.derToPem(keyBuffer, 'PRIVATE KEY');
                    privateKeyForge = forge.pki.privateKeyFromPem(pemKey);
                } catch (error2) {
                    // 如果都失败，抛出更详细的错误信息
                    throw new Error(`私钥解析失败: ${error1.message}。尝试 PEM 格式也失败: ${error2.message}`);
                }
            }

            // 分段解密
            const decryptedChunks = [];
            let offset = 0;
            const inputLen = dataArray.length;

            while (inputLen - offset > 0) {
                let chunkSize;
                if (inputLen - offset > this.MAX_DECRYPT_BLOCK) {
                    chunkSize = this.MAX_DECRYPT_BLOCK;
                } else {
                    chunkSize = inputLen - offset;
                }

                // 提取当前块
                const chunk = dataArray.slice(offset, offset + chunkSize);
                
                // 将 Uint8Array 转换为 forge 需要的格式（二进制字符串）
                const chunkBinary = Array.from(chunk)
                    .map(byte => String.fromCharCode(byte))
                    .join('');
                
                // 使用 PKCS1 v1.5 padding 解密（与 Java 保持一致）
                const decryptedBinary = privateKeyForge.decrypt(chunkBinary, 'RSAES-PKCS1-V1_5');
                
                // 将解密后的二进制字符串转换回 Uint8Array
                const decrypted = new Uint8Array(
                    decryptedBinary.split('').map(c => c.charCodeAt(0))
                );

                decryptedChunks.push(decrypted);
                offset += chunkSize;
            }

            // 合并所有解密块
            const totalLength = decryptedChunks.reduce((sum, chunk) => sum + chunk.length, 0);
            const result = new Uint8Array(totalLength);
            let resultOffset = 0;
            for (const chunk of decryptedChunks) {
                result.set(chunk, resultOffset);
                resultOffset += chunk.length;
            }

            return result;
        } catch (error) {
            throw new Error(`RSA解密失败: ${error.message}`);
        }
    }

    /**
     * 将 DER 格式的密钥转换为 PEM 格式
     * @param {Uint8Array|ArrayBuffer} derKey - DER 格式的密钥
     * @param {string} type - 密钥类型 ('PRIVATE KEY' 或 'PUBLIC KEY')
     * @returns {string} PEM 格式的密钥
     */
    static derToPem(derKey, type) {
        let uint8Array;
        if (derKey instanceof ArrayBuffer) {
            uint8Array = new Uint8Array(derKey);
        } else if (derKey instanceof Uint8Array) {
            uint8Array = derKey;
        } else {
            uint8Array = new Uint8Array(derKey);
        }
        
        // 使用 forge 的 Base64 编码
        const binary = Array.from(uint8Array)
            .map(byte => String.fromCharCode(byte))
            .join('');
        const base64Key = forge.util.encode64(binary);
        const chunks = base64Key.match(/.{1,64}/g) || [];
        return `-----BEGIN ${type}-----\n${chunks.join('\n')}\n-----END ${type}-----`;
    }
}

/**
 * XSS 清理工具类
 */
class XssHttpServletRequestUtils {
    /**
     * 根据参数名称判断是否需要过滤
     * @param {string} parameter - 参数名称
     * @returns {boolean} true 表示不过滤
     */
    static needSkipCleanXss(parameter) {
        const skipParams = [
            'floginName',
            'password',
            'tradePwd',
            'originPwd',
            'newPwd',
            'reNewPwd'
        ];
        return skipParams.includes(parameter);
    }

    /**
     * 清理 XSS 攻击代码
     * @param {string} value - 要清理的值
     * @returns {string} 清理后的值
     */
    static cleanXSS(value) {
        if (!value) return value;

        // 移除 eval(...)
        value = value.replace(/eval\(.*?\)/g, '');
        
        // 移除 javascript: 协议
        value = value.replace(/["'][\s]*javascript:(.*?)["']/g, '""');
        
        // 替换 script 关键字
        value = value.replace(/script/gi, '-');
        
        // 替换 src= 属性
        value = value.replace(/src=/gi, '-');
        
        // 检查 SQL 关键字
        const sqlKeywords = ['update', 'detele', 'select', 'truncate', 'insert'];
        if (sqlKeywords.includes(value.toLowerCase())) {
            value = '';
        }
        
        // 替换分号
        value = value.trim().replace(/;/g, '；');
        
        return value;
    }
}

/**
 * App请求参数解密工具类
 */
class AppRequestDecryptor {
    /**
     * 解密 App 请求参数
     * @param {string} requestParam - Base64 编码的加密请求参数
     * @param {string} [privateKey] - RSA 私钥（可选，默认使用 EncryptionConstant.APP_REQUEST_RSA_PRIVATE_PAIR）
     * @returns {Object} 解密后的参数对象，格式为 { paramName: [value] }
     * @throws {Error} 如果解密失败或参数为空
     */
    static decrypt(requestParam, privateKey = null) {
        const parameterMap = {};

        try {
            // 检查参数是否为空
            if (!requestParam || requestParam.trim() === '') {
                throw new Error('app签名校验失败! param is null');
            }

            // URL 解码还原（在 Base64 解码之前）
            let urlDecodedParam = requestParam;
            try {
                urlDecodedParam = decodeURIComponent(requestParam);
            } catch (e) {
                // 如果 URL 解码失败，使用原始字符串
                console.warn('URL 解码失败，使用原始字符串:', e);
            }

            // Base64 解码
            const decoded = Base64.decode(urlDecodedParam);

            // RSA 私钥解密
            const rsaPrivateKey = privateKey || EncryptionConstant.APP_REQUEST_RSA_PRIVATE_PAIR;
            const decodedData = RSAUtils.decryptByPrivateKey(decoded, rsaPrivateKey);

            // 将 Uint8Array 转换为字符串并 URL 解码
            let decrypt = '';
            try {
                // 使用 TextDecoder 将 Uint8Array 转换为 UTF-8 字符串
                const decoder = new TextDecoder('utf-8');
                decrypt = decoder.decode(decodedData);
            } catch (e) {
                // 降级方案：手动转换
                decrypt = Array.from(decodedData)
                    .map(byte => String.fromCharCode(byte))
                    .join('');
            }
            
            if (decrypt) {
                try {
                    decrypt = decodeURIComponent(decrypt);
                } catch (e) {
                    // 如果 URL 解码失败，使用原始字符串
                    console.warn('URL 解码失败，使用原始字符串:', e);
                }
            }

            // 解析参数
            const parameters = decrypt.split('&');
            for (const _parameter of parameters) {
                const parameter = _parameter.split('=');
                const name = parameter[0];

                if (parameter.length >= 2 && name.toLowerCase() !== 'logintoken') {
                    // 获取参数值（处理值中包含 = 的情况）
                    const value = _parameter.substring(_parameter.indexOf('=', 0) + 1);

                    // XSS 清理
                    let cleanValue = value;
                    if (!XssHttpServletRequestUtils.needSkipCleanXss(name)) {
                        cleanValue = XssHttpServletRequestUtils.cleanXSS(value);
                    }

                    // 存储参数（格式为数组，与 Java 版本保持一致）
                    parameterMap[name] = [cleanValue];
                }
            }

            return parameterMap;
        } catch (error) {
            console.error('解密失败:', error);
            throw new Error(`app签名校验失败! ${error.message}`);
        }
    }

    /**
     * 解密并打印结果（对应原 Java main 方法的功能）
     * @param {string} requestParam - Base64 编码的加密请求参数
     * @param {string} [privateKey] - RSA 私钥（可选）
     */
    static decryptAndPrint(requestParam, privateKey = null) {
        console.log('解密前：', requestParam);
        
        const parameterMap = this.decrypt(requestParam, privateKey);
        
        console.log('解密后：', JSON.stringify(parameterMap, null, 2));
        
        return parameterMap;
    }
}

// 导出模块（ES6 模块格式）
export {
    AppRequestDecryptor,
    Base64,
    RSAUtils,
    XssHttpServletRequestUtils,
    EncryptionConstant
};

